.wrap{padding:  0;
  background-color: #222222;
  padding: 10px;
 
 }
.item{display: flex;}
    .item:hover{transform: none;}

.wrapBlock {
    width: 860px;
    display: flex;
    
   
  }
  .blockRi {

    display: flex;
    justify-content: center;
    width: 510px;
    min-height: 400px;
    background-color: rgba(24, 24, 24, 255);
    margin-left: 5px;
  }
  .blockRiLef {
    width: 150px;
    font-weight: bold;
    font-size: 18px;
  }
  .overviewWrap {
    position: relative;
   
    width: 860px;
    background-color: rgba(34, 34, 34, 255);
  }
  .overview {
   margin-top: 5px;
    padding: 20px 10px;
    color: rgba(153, 141, 123, 255);
    border-radius: 0 0 10px 10px;
    font-weight: normal;
  }
  .span {
    margin-left: 10px;
    display: block;
    font-weight: normal;
    font-size: 25px;
  }
  
  .title {
    color: rgba(218, 218, 218, 255);
    padding-left: 15px;
    width: 350px;
    height: 80px;
    display: flex;
    align-items: center;
    margin: 0;
  }
  .title::first-letter{
    font-size: 30px;
    
}
  .info {
    color: rgba(218, 218, 218, 255);
    padding-left: 15px;
    width: 350px;
    height: 80px;
    background-color: rgba(34, 34, 34, 255);
    display: flex;
  margin: 0;
    align-items: center;
  }
  .reviews {color:rgba(218, 218, 218, 255);}
  .cast{color:rgba(218, 218, 218, 255);}
  .blockInfoLeft {
    color: rgba(153, 141, 123, 255);
    padding-left: 15px;
    width: 150px;
    height: 80px;
    background-color: rgba(34, 34, 34, 255);
    display: flex;
    align-items: center;
    margin: 0;
  }
  .blockInfo {
   margin: 0;
    color: rgba(153, 141, 123, 255);
    padding-left: 15px;
    width: 150px;
    height: 80px;
    display: flex;
    align-items: center;
  }
  .span::first-letter {
    color: red;
  }
  .wrapCastReviews {
    display: flex;
    justify-content: space-between;
    width: 150px;
    color: rgba(218, 218, 218, 255);
    font-size: 20px;
    position: absolute;
    top: 20px;
    right: 50px;
    padding-bottom: 20px;
  }
  

